.invoice-wrapper {
	position: absolute;
	left: 0;
	bottom: 0;
	right: 0;
	top: 0;
	background: #f8f8f8;
	padding: 80px 0;

	@media print {
		padding: 0; }

	$color : #929292;
	$bold: 700;

	p,a,td {
		font-size: 12px;
		line-height: 15px; }

	.paper-invoice {
		width: 722px;
		background: #FFF;
		margin: 0 auto;
		padding: 30px;
		-webkit-box-shadow: 0px 0px 14px 0px rgba(50, 50, 50, 0.75);
		-moz-box-shadow:    0px 0px 14px 0px rgba(50, 50, 50, 0.75);
		box-shadow:         0px 0px 14px 0px rgba(50, 50, 50, 0.75);
		@extend .box-sizing;
		padding-bottom: 60px;

		.invoice-header {
			@extend .clearfix;

			img {
				float: left; }

			h2 {
				float: right;
				font-size: 24px;
				line-height: 53px;
				font-weight: $bold;
				text-transform: uppercase; } }

		.left-info {
			float: left;
			width: 47.5%; }

		.right-info {
			float: right;
			width: 47.5%;

			table {
				td {
					padding: 10px;

					&:first-of-type {
						text-align: right;
						width: 110px;
						text-transform: uppercase;
						font-weight: $bold;
						border-right: 1px solid #ccc; }

					&:last-of-type {

						p {
							color: $color;

							&.name {
								font-weight: $bold; } } } } } }



		.info-top {
			@extend .clearfix;
			padding-top: 10px;

			.left-info {
				padding-top: 10px;

				p,a {
					color: $color;

					&.phone {
						font-weight: $bold; } }

				a:hover {
					text-decoration: underline; } } }

		.info-bottom {
			padding-top: 20px;
			@extend .clearfix;

			.left-info {
				padding-top: 10px;
				text-transform: uppercase;

				table {
					td:first-of-type {
						font-weight: $bold;
						display: inline-block;
						width: 85px; }

					td:last-of-type {
						color: $color;
						display: inline-block; } } } }

		.cart-invoice {
			width: 100%;
			margin-top: 30px;

			thead {
				td {
					text-align: center;
					border-left: 1px solid #ccc;
					border-bottom: 1px solid #ccc;
					padding: 10px;
					font-weight: $bold;
					text-transform: uppercase;

					&:first-of-type {
						border-left: 0;
						padding-left: 0; }

					&.description {
						text-align: left; } } }

			tbody {
				td {
					vertical-align: middle;
					padding: 10px;
					border-left: 1px solid #ccc;
					border-bottom: 1px solid #ccc;
					text-align: center;

					&:first-of-type {
						width: 65px;
						border-left: 0;

						img {
							width: 65px;
							display: block;
							margin: 0 auto; } }

					&.description {
						width: 296px;
						text-align: left;

						.sku {
							text-transform: uppercase; }

						.name {
							text-transform: uppercase;
							font-weight: $bold; }

						.description {
							span {
								color: $color; } } } } } }

		.total-invoice {
			width: 50%;
			margin-left: 50%;
			margin-top: 30px;

			td {
				text-align: right;
				padding-bottom: 3px;

				&:first-of-type {
					font-weight: $bold; } }

			tr.total td {
				font-size: 16px;
				padding-top: 15px;
				border-top: 1px solid #ccc;
				font-weight: $bold;

				&:first-of-type {
					text-transform: uppercase; } } } } }




