.header {
	position: relative;
	z-index: 100;
	background: #FFF;

	.top-nav {
		padding: 25px 0 30px;
		background: #000;
		@extend .clearfix;

		.login-link {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			text-transform: uppercase;
			color: $gold;
			background: none;
			border: none;
			font-weight: 800;
			font-size: 16px;

			@include mq(480px) {
				font-size: 14px; }

			&:before {
				content: '';
				display: inline-block;
				margin-right: 15px;
				vertical-align: bottom;
				width: 16px;
				height: 20px;
				background: url(../img/header-sprite.png) no-repeat; }

			&.connected {
				color: $green;

				&:before {
					background-position: 0 -20px; } } }

		.disconnect {
			display: inline-block;
			vertical-align: middle;
			padding: 10px 15px;
			color: #FFF;
			background: $gold;
			text-transform: uppercase;
			font-weight: 600;
			font-size: 16px;
			margin-left: 20px;
			position: relative;
			top: 3px;
			border-radius: 5px;
			-moz-border-radius: 5px;
			-ms-border-radius: 5px;

			& + ul {
				margin-top: 15px; } }

		ul {
			float: right;

			@include mq(480px) {
				display: none; }

			li {
				display: inline-block;

				&:after {
					content: '|';
					color: $gold;
					display: inline-block;
					margin: 0 10px;
					font-weight: 600;
					font-family: $source; }

				&:last-of-type:after {
					display: none; }

				a {
					text-transform: uppercase;
					color: #FFF;
					font-weight: 600;
					font-size: 16px; } } } }

	.main-nav {
		position: relative;
		border-top: 9px solid $gold;
		border-bottom: 9px solid $gold;
		padding: 30px 0;
		@extend .clearfix;

		li {
			display: inline-block;
			margin-right: 30px;

			@include mq(1024px) {
				margin-right: 15px; }

			a {
				font-size: 20px;
				text-transform: uppercase;
				color: #000;
				font-weight: 800;

				@include mq(1280px) {
					font-size: 20px; } } }

		.logo {
			position: absolute;
			z-index: 75;
			left: 50%;
			top: -60px;
			@include transform(translate(-50%,0));

			@include mq(1280px) {
				top: -40px;

				img {
					width: 300px; } }

			@include mq(1024px) {
				top: -25px;
				left: 5%;
				@include transform(translate(0,0));

				img {
					width: 225px; } }

			@include mq(480px) {
				left: 50%;
				@include transform(translate(-50%,0)); } }

		ul {
			@include mq(768px) {
				display: none; }

			&.left-nav {
				float: left;

				li:last-of-type {
					margin-right: 0; }

				@include mq(1024px) {
					float: right; } }


			&.right-nav {
				float: right;
				text-align: right;

				li:last-of-type {
					margin-right: 0; }

				@include mq(1024px) {
					margin-right: 15px; } } }

		.menu-btn {
			float: right;
			text-align: left;
			background: url(../img/menu-sprite.png) no-repeat;
			border: none;
			width: 30px;
			height: 21px;
			display: none;
			text-indent: -9999px;

			@include mq(768px) {
				display: inline-block; }

			@include mq(480px) {
				position: relative;
				top: -69px;
				background-position: 0 -21px;
				@include transform(translate(0,-100%)); } } }

	.login-content {
		overflow: hidden;
		position: absolute;
		z-index: 70;
		width: 0;
		top: 100%;
		left: 50%;
		@include transform(translate(-50%,60px));
		padding: 0;
		opacity: 0;
		visibility: hidden;
		transition: opacity 250ms ease, width 250ms ease, border 250ms ease, padding 250ms ease, visibility 0ms ease 250ms;
		margin-top: 150px;

		.login-header {
			position: relative;
			opacity: 0;
			transition: opacity 250ms ease;

			.close-btn {
				border: none;
				position: absolute;
				top: 10px;
				right: 20px;
				background: url(../img/close-btn.png) no-repeat;
				width: 44px;
				height: 44px;
				display: block;
				text-indent: -9999px; }

			h3 {
				display: inline-block;
				margin-left: 125px;
				font-size: 60px;
				color: #FFF;
				font-weight: bold;
				text-transform: uppercase;
				opacity: 0;
				@include transform(translate(0,-50px));
				transition: opacity 250ms ease 500ms, -webkit-transform 250ms ease 500ms;

				&:before {
					background: url(../img/locker-login.png) no-repeat;
					content: '';
					width: 31px;
					height: 38px;
					display: inline-block;
					vertical-align: top;
					margin-right: 25px;
					position: relative;
					top: 12px; } } }

		form {
			position: relative;
			display: block;
			width: 85%;
			margin: 0 auto;
			opacity: 0;
			transition: opacity 250ms ease 250ms;

			.login-input {
				background: $green;
				@include transform(skew(-20deg,0deg));
				padding: 50px 0;
				margin-top: 30px;
				overflow: hidden; }

			.input-block {
				margin-top: 40px;
				@include transform(translate(0,-50px) skew(20deg,0deg));
				opacity: 0;
				transition: -webkit-transform 250ms ease 500ms, opacity 250ms ease 500ms;

				&:first-of-type {
					margin-top: 0; }

				label {
					text-align: right;
					margin-right: 50px;
					width: 250px;
					text-transform: uppercase; }

				.input-wrapper {
					width: 350px; } }

			a {
				display: inline-block;
				margin-top: 65px;
				font-size: 24px;
				color: #FFF; }

			.button-link {
				float: right;
				margin-top: 40px;
				margin-right: 70px; } }


		&.trigger {
			width: 900px;
			opacity: 1;
			visibility: visible;
			transition-delay: 0ms;
			-webkit-transition-delay: 0ms;
			-ms-transition-delay: 0ms;

			.login-header {

				h3,p {
					opacity: 1;
					@include transform(translate(0,0)); } }

			form, .login-header {
				opacity: 1; }

			form .input-block {
				opacity: 1;
				@include transform(translate(0,0) skew(20deg,0deg)); } } }


	.inscription-content {
		overflow: hidden;
		position: absolute;
		z-index: 70;
		border: 8px solid transparent;
		background: rgba(0,0,0,0.8);
		width: 50%;
		height: 0;
		top: 100%;
		left: 50%;
		@include transform(translate(-50%,60px));
		padding: 0;
		@include border-radius(15px);
		opacity: 0;
		visibility: hidden;
		transition: opacity 500ms ease, width 500ms ease, border 500ms ease, padding 500ms ease, visibility 0ms ease 500ms;
		-webkit-transition: opacity 500ms ease, width 500ms ease, border 500ms ease, padding 500ms ease, visibility 0ms ease 500ms;
		-ms-transition: opacity 500ms ease, width 500ms ease, border 500ms ease, padding 500ms ease, visibility 0ms ease 500ms;

		.captcha-wrapper {
			display: block;
			width: 100%;
			@extend .clearfix;
			margin: 30px 0 0;

			.g-recaptcha {
				float: right; } }


		.inscpription-header {
			position: relative;
			text-align: center;
			opacity: 0;
			transition: opacity 250ms ease 500ms;

			.close-btn {
				border: none;
				position: absolute;
				top: -105px;
				right: 20px;
				background: url(../img/close-btn.png) no-repeat;
				width: 44px;
				height: 44px;
				display: block;
				text-indent: -9999px; }

			h3 {
				width: 900px;
				font-size: 66px;
				color: #FFF;
				font-weight: bold;
				text-transform: uppercase;
				opacity: 0;
				@include transform(translate(0,-50px));
				transition: opacity 250ms ease 750ms, -webkit-transform 250ms ease 750ms; }

			p {
				width: 900px;
				color: #FFF;
				margin-top: 30px;
				font-size: 18px;
				opacity: 0;
				@include transform(translate(0,-50px));
				transition: opacity 250ms ease 950ms, -webkit-transform 250ms ease 950ms;


				&.step {
					margin-top: 10px;
					font-size: 32px;
					color: $green;
					text-transform: uppercase;
					opacity: 0;
					transition-delay: 850ms;

					span {
						color: #FFF;
						display: inline-block;
						margin-left: 40px; } } } }

		form {
			position: relative;
			display: block;
			width: 650px;
			margin: 0 auto;
			opacity: 0;
			transition: opacity 250ms ease 500ms;

			.input-block {
				margin-top: 40px;
				@include transform(translate(0,-50px));
				opacity: 0;
				transition: -webkit-transform 250ms ease 1000ms, opacity 250ms ease 1000ms;

				&.ville {
					z-index: 300; }

				ul {
					width: 448px;
					max-height: 600px;
					overflow-y: scroll;

					.city-result {
						display: block;
						margin-right: 0; } }

				label {
					text-align: right;
					margin-right: 50px;
					width: 150px; }

				.input-wrapper {
					width: 450px;

					&.select {
						position: relative;

						select {
							width: 500px; }

						&.cut {
							width: 200px;

							select {
								width: 250px; } } } } }

			.button-link {
				float: right;
				margin-top: 40px; } }

		&.trigger {
			padding: 125px 0;
			width: 900px;
			height: auto;
			border: 8px solid #c7c8ca;
			opacity: 1;
			visibility: visible;
			transition-delay: 0ms;
			-webkit-transition-delay: 0ms;
			-ms-transition-delay: 0ms;

			.inscpription-header {

				h3,p {
					opacity: 1;
					@include transform(translate(0,0)); } }

			form, .inscpription-header {
				opacity: 1; }

			form .input-block {
				opacity: 1;
				@include transform(translate(0,0)); } } }

	.mobile-menu {
		position: fixed;
		z-index: 1000;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		overflow-y: scroll;
		background: rgba(4,4,4,0.9);
		display: none;
		opacity: 0;
		visibility: hidden;
		@include transform(translate(100%,0));
		transition: opacity 500ms ease, transform 500ms ease, visibility 0ms ease 500ms;
		-webkit-transition: opacity 500ms ease, -wekbit-transform 500ms ease, visibility 0ms ease 500ms;
		-webkit-transition: opacity 500ms ease, transform 500ms ease, visibility 0ms ease 500ms;

		&::-webkit-scrollbar {
			display: none; }

		@include mq(768px) {
			display: block; }

		.close-btn {
			position: absolute;
			display: block;
			top: 20px;
			right: 20px;
			background: none;
			border: none;
			width: 20px;
			height: 20px;

			&:after, &:before {
				position: absolute;
				top: 2.5px;
				left: 10px;
				content: '';
				display: block;
				width: 20px;
				height: 5px;
				background: $gold; }

			&:before {
				@include transform(rotate(45deg)); }

			&:after {
				@include transform(rotate(-45deg)); } }

		.logo {
			max-width: 50%;
			display: block;
			margin: 50px auto;

			@include mq(480px) {
				max-width: 80%; }

			img {
				width: 100%; } }

		ul {
			width: 100%;
			text-align: center;
			padding-bottom: 50px;

			li {

				a {
					font-weight: bold;
					font-size: 24px;
					display: block;
					color: #FFF;
					padding: 15px 0;
					border-top: 1px solid $gold;
					border-bottom: 1px solid $gold - 50; }

				&:last-of-type a {
					border-bottom: 2px solid $gold; }

				&.top-nav-link {
					display: none;

					@include mq(480px) {
						display: block; } } } }

		&.trigger {
			opacity: 1;
			visibility: visible;
			@include transform(translate(0,0));
			transition-delay: 0ms;
			-webkit-transition-delay: 0ms;
			-ms-transition-delay: 0ms; } } }




