.form-page {
	background: #484848 url(../img/bg_form.jpg) no-repeat top center;
	background-attachment: fixed;
	position: relative;

	.form-header {
		text-align: center;
		padding-top: 150px;

		h2 {
			text-transform: uppercase;
			color: #FFF;
			font-size: 42px;
			font-weight: bold;

			span {
				color: $green; } }

		p {
			color: #FFF;
			font-size: 24px;
			font-weight: lighter;
			margin-top: 20px;
			line-height: 125%; } }

	#form-complete {
		padding: 75px 0 150px;

		.step-wrapper {
			@extend .box-sizing;
			width: 100%;
			margin-top: 20px;

			.step-content {
				display: none; }


			.step-header {
				@extend .box-sizing;
				position: relative;
				background: $green;
				color: #FFF;
				text-transform: uppercase;
				font-weight: bold;
				padding: 20px;
				font-size: 32px;
				cursor: pointer;
				transition: transform 250ms ease, background 250ms ease;
				-webkit-transition: -webkit-transform 250ms ease, background 250ms ease;
				-ms-transition: transform 250ms ease, background 250ms ease;
				@include transform(skew(0,0));

				.header-content {
					position: relative;
					transition: transform 250ms ease;
					-webkit-transition: -webkit-transform 250ms ease;
					-ms-transition: transform 250ms ease; }

				&.not-complete {
					background: $gold; }

				&.abonnement {
					background: #df5151; }

				&.disable {
					opacity: 0.7;
					background: #a7a7a7; }

				h2 {
					display: block;
					transform: transition 250ms ease;
					-webkit-transition: -webkit-transform 250ms ease;
					-ms-transition: transform 250ms ease; }

				ul {
					position: absolute;
					right: 20px;
					top: 50%;
					@include transform(translate(0,-50%));

					li {
						display: inline-block;
						vertical-align: middle;
						margin-right: 15px;
						font-size: 18px;
						text-transform: none;
						font-weight: lighter;

						&:last-of-type {
							margin-right: 0; }

						&.step-status {
							background: url(../img/arrow-circle.png) no-repeat;
							background-size: contain;
							width: 35px;
							height: 35px;
							@include transform(rotate(-90deg));
							transition: -webkit-transform 250ms ease; } } }

				&.trigger {
					@include transform(skew(-15deg,0));

					.header-content {
						@include transform(skew(15deg,0)); }

					ul li.step-status {
						@include transform(rotate(-270deg)); } } }

			.step-content {
				padding: 50px;
				background: rgba(0,0,0,0.5);

				.button-link {
					padding: 15px 30px;

					span {
						font-size: 24px; } }

				.checkbox-wrapper {
					position: relative;
					top: -30px;

					input {
						position: absolute;
						left: -9999px;

						& + label {
							position: relative;
							padding-left: 25px;
							cursor: pointer;
							padding-top: 3px;
							color: #FFF;
							display: block;
							font-size: 18px;

							a {
								color: $green; }

							&:before {
								content: '';
								position: absolute;
								z-index: 10%;
								left: 0;
								top: 2px;
								width: 17px;
								height: 17px;
								border: 1px solid #CCC;
								background: #FFF; }

							&:after {
								content: "";
								display: block;
								border: 3px solid #191919;
								border-top: none;
								border-left: none;
								position: absolute;
								width: 4px;
								height: 9px;
								z-index: 2;
								top: 4.5px;
								left: 6px;
								-webkit-transform: rotate(45deg);
								-ms-transform: rotate(45deg);
								transform: rotate(45deg);
								transition: opacity 250ms ease, transform 250ms ease;
								-webkit-transition: opacity 250ms ease, -webkit-transform 250ms ease;
								-ms-transition: opacity 250ms ease, transform 250ms ease; } }

						&:not(:checked) + label:after {
							opacity: 0;
							-webkit-transform: rotate(45deg) scale(0);
							-ms-transform: rotate(45deg) scale(0);
							transform: rotate(45deg) scale(0); }

						&:checked + label:after {
							opacity: 1;
							-webkit-transform: rotate(45deg) scale(1);
							-ms-transform: rotate(45deg) scale(1);
							transform: rotate(45deg) scale(1); } } }

				.input-block {
					display: inline-block;
					width: 47.5%;
					margin-right: 5%;
					margin-bottom: 50px;

					&:nth-of-type(2n) {
						margin-right: 0; }

					&.blank {
						margin-right: 10%; }

					&.address {
						width: 70%; }

					&.app {
						width: 25%; }

					@include mq(768px) {
						width: 100%;
						margin-right: 0;
						margin-bottom: 35px;

						&.address {
							margin-right: 5%; } }

					label {
						color: #FFF;
						font-size: 24px;
						padding-left: 8px; }

					.input-wrapper {
						width: 100%;
						margin-top: 5px;

						&.select {

							select {
								width: 125%; } } } }

				.section-paiement {
					position: relative;
					display: inline-block;
					vertical-align: top;
					width: 47.5%;
					margin-right: 5%;

					&:nth-of-type(2n) {
						margin-right: 0;

						&:before {
							content: '';
							height: 100%;
							width: 5px;
							position: absolute;
							top: 0;
							left: -30px;
							background: url(../img/gray-dot.png) repeat-y; }

						p {
							color: $green;
							font-size: 24px;
							font-weight: bold;
							font-size: 36px;
							margin-top: 0; } }

					.price-wrapper {
						display: block;
						text-transform: uppercase; }

					.tax-wrapper {
						display: inline-block;
						vertical-align: top;
						width: 47.5%;
						margin-right: 5%;
						margin-top: 30px;

						&:nth-of-type(2n) {
							margin-right: 0; } }

					.total-wrapper {
						margin: 50px 0;

						h3 {
							font-size: 36px; } }


					.input-block {
						width: 90%;
						margin-right: 0;
						margin-top: 10px;
						width: 50%;

						&.promo-code {}

						&:nth-of-type(2), &:nth-of-type(3) {
							label {
								display: block; }

							.input-wrapper {
								width: 47.5%;
								margin-right: 5%;

								&:last-of-type {
									margin-right: 0; } } } }

					h3 {
						font-size: 28px;
						font-weight: bold;
						text-transform: uppercase;
						color: #FFF; }

					p {
						line-height: 125%;
						font-size: 18px;
						color: #FFF;
						margin-top: 20px;

						&.price {
							display: inline-block;
							background: $green;
							color: #FFF;
							font-weight: bold;
							font-size: 42px;
							margin-top: 10px;
							padding: 10px 20px;
							@include transform(skew(-15deg,0deg));

							span {
								display: block;
								@include transform(skew(15deg,0deg)); } } }




					.paiement-method {
						margin-top: 75px;
						display: inline-block;

						img {
							max-width: 256px; }

						p {
							display: block;
							text-align: right;
							margin-top: 5px;
							font-size: 15px;
							color: rgba(255,255,255,0.8); } }

					ul {
						margin-top: 20px;

						li {
							margin-top: 5px;

							a {
								display: block;
								background: rgba(255,255,255,0.1);
								padding: 10px;
								color: $green;
								transition: background 250ms ease, color 250ms ease;
								-webkit-transition: background 250ms ease, color 250ms ease;
								-ms-transition: background 250ms ease, color 250ms ease;

								span:last-of-type {
									float: right; }

								&:hover {
									background: $green;
									color: #FFF; } } } } } } }





		.save-change {
			position: absolute;
			right: 30px;
			padding: 15px 20px;
			z-index: 1000;

			span {
				font-size: 18px;
				text-transform: none; } } } }


