.scroll-top {
	position: fixed;
	right: 20px;
	bottom: 20px;
	display: block;
	text-indent: -9999px;
	width: 50px;
	height: 50px;
	border-radius: 50%;
	-moz-border-radius: 50%;
	-ms-border-radius: 50%;
	background: $green;
	z-index: 5000;

	&:before,&:after {
		content: '';
		background: #FFF;
		position: absolute;
		width: 20px;
		height: 7px;
		border-radius: 15px;
		-moz-border-radius: 15px;
		-ms-border-radius: 15px;
		top: 20px; }

	&:before {
		right: 10px;
		@include transform(rotate(45deg)); }

	&:after {
		left: 10px;
		@include transform(rotate(-45deg)); } }

.faq-intro {
	background: url(../img/bg-faq.jpg) no-repeat center;
	background-size: cover;
	background-attachment: fixed;
	padding: 175px 0 100px;
	text-align: center;
	position: relative;

	&:after,&:before {
		content: '';
		height: 5px;
		width: 100%;
		position: absolute;
		left: 0;
		background: url(../img/white-dot.png) repeat-x;
		background-position: 0 -5px; }

	&:before {
		bottom: 20px; }

	&:after {
		top: 20px; }

	@include mq(1280px) {
		padding: 125px 0 75px; }

	@include mq(1024px) {
		padding: 50px 0; }

	h2 {
		color: #FFF;
		font-size: 42px;
		text-transform: uppercase;
		font-weight: bold;

		@include mq(1280px) {
			font-size: 38px; }

		@include mq(1024px) {
			font-size: 32px; } }

	p {
		margin-top: 10px;
		color: #FFF;
		font-size: 18px; }

	ul {
		margin-top: 40px;

		li {
			display: block;
			margin: 5px 0;

			a {
				font-weight: bold;
				font-size: 18px;
				color: $green;
				transition: color 250ms ease;
				-webkit-transition: color 250ms ease;
				-ms-transition: color 250ms ease;

				&:hover {
					color: #FFF; } } } } }

.faq-content {
	padding: 25px 0 75px;

	@include mq(1024px) {
		text-align: center; }

	.question {
		padding-top: 50px;

		h3 {
			display: block;
			padding-bottom: 5px;
			border-bottom: 1px dashed #CCC;
			text-transform: uppercase;
			font-weight: bold;
			color: $green;
			font-size: 32px;
			transition: color 250ms ease;
			-webkit-transition: color 250ms ease;
			-ms-transition: color 250ms ease;

			@include mq(1024px) {
				font-size: 24px; } }

		.answer {
			font-size: 18px;
			color: #000;
			font-weight: normal;
			margin-top: 20px;
			line-height: 125%;

			p {
				font-size: 18px;
				color: #000;
				font-weight: normal;
				margin-top: 10px;
				line-height: 125%;

				&:first-of-type {
					margin-top: 0; } } }

		&.current h3 {
			color: #000; } } }

.liste-des-gagnants {
	.year-container {

		&:first-of-type h2 {
			margin-top: 50px; }

		h2 {
			display: block;
			padding-bottom: 5px;
			border-bottom: 1px dashed #CCC;
			text-transform: uppercase;
			font-weight: bold;
			color: $green;
			font-size: 38px;
			transition: color 250ms ease;
			-webkit-transition: color 250ms ease;
			-ms-transition: color 250ms ease;
			margin-top: 100px;

			@include mq(1024px) {
				font-size: 24px; } }

		.description {
			margin-top: 100px;

			&:first-of-type {
				margin-top: 50px; }

			h3 {
				color: $green;
				font-size: 24px; }

			table {
				width: 100%;
				margin-top: 10px;

				td {
					padding: 15px;
					background: #fdfdfd !important;
					font-size: 18px;

					&:first-of-type {
						font-weight: bold; }

					&:nth-of-type(3) {
						color: $green;
						font-weight: bold; }

					@include mq(1024px) {
						width: 100%;
						box-sizing: border-box;
						-moz-box-sizing: border-box;
						-webkit-box-sizing: border-box;
						padding: 10px; } }

				tr:nth-of-type(2n) td {
					background: #f5f5f5 !important; } }

			@include mq(1024px) {
				table, td, tr, tbody {
					display: block;
					width: 100%; } } } } }




