.concours.block-content {
	padding-bottom: 75px;

	.concours-intro {
		background: url(../img/bg-concours.jpg) no-repeat center;
		background-size: cover;
		background-attachment: fixed;
		padding: 175px 0 100px;
		text-align: center;
		position: relative;

		&:after,&:before {
			content: '';
			height: 5px;
			width: 100%;
			position: absolute;
			left: 0;
			background: url(../img/white-dot.png) repeat-x;
			background-position: 0 -5px; }

		&:before {
			bottom: 20px; }

		&:after {
			top: 20px; }

		@include mq(1280px) {
			padding: 125px 0 75px; }

		@include mq(1024px) {
			padding: 50px 0; }

		h2 {
			color: #FFF;
			font-size: 42px;
			text-transform: uppercase;
			font-weight: bold;

			@include mq(1280px) {
				font-size: 38px; }

			@include mq(1024px) {
				font-size: 32px; } }

		p {
			margin-top: 10px;
			color: #FFF;
			font-size: 24px;

			span {
				font-weight: bold;
				color: $green; }

			&.price-range {
				margin-top: 5px; } } }

	.concours {
		position: relative;
		padding: 75px 0;

		&:after {
			content: '';
			width: 100%;
			position: absolute;
			left: 0;
			bottom: 0;
			height: 5px;
			background: url(../img/gray-dot.png) repeat-x;
			background-position: -5px; }

		&:last-of-type:after {
			display: none; }


		.image-wrapper {
			width: 25%;
			margin-right: 5%;
			display: inline-block;
			vertical-align: top;
			position: relative;
			padding-bottom: 17.5%;
			overflow: hidden;
			border: 1px solid #CCC;
			@extend .box-sizing;

			@include mq(1024px) {
				width: 100%;
				margin-right: 0;
				border: none;
				padding-bottom: 0; }


			img {
				position: absolute;
				top: 50%;
				left: 50%;
				@include transform(translate(-50%,-50%));
				max-height: 80%;

				&.concouria {
					max-width: 50%; }

				@include mq(1024px) {
					position: static;
					display: block;
					@include transform(translate(0,0));
					top: auto;
					left: auto;
					margin: 0 auto 30px;
					max-height: 200px; } } }

		.description {
			width: 70%;
			display: inline-block;
			vertical-align: middle;

			@include mq(1024px) {
				width: 100%;
				text-align: center; }

			h3 {
				display: block;
				font-size: 32px;
				color: $gray;
				font-weight: bold;

				span {
					float: right;
					color: $green;

					@include mq(1024px) {
						display: block;
						float: none;
						margin: 5px 0; } } }

			.date {
				margin-top: 10px;
				color: $green;
				font-size: 18px;

				span {
					font-weight: bold; } }

			div {
				margin-top: 20px;

				p {
					font-size: 18px;
					line-height: 125%;
					margin-top: 20px; }

				ul {
					padding-left: 20px;
					list-style-type: disc;

					li {
						margin: 5px 0;
						font-size: 18px; } } } } } }



