.intro {
	position: relative;
	z-index: 50;
	background: #040404 url(../img/bg-intro.jpg) no-repeat top center;
	background-size: cover;
	height: 775px;
	overflow: hidden;

	@include mq(1440px) {
		height: 500px; }

	@include mq(750px) {
		height: auto;
		background: #040404; }

	&:before {
		position: absolute;
		z-index: 50;
		content: '';
		display: block;
		width: 100%;
		height: 95%;
		top: 2.5%;
		border-top: 3px dotted #FFF;
		border-bottom: 3px dotted #FFF; }


	.image-wrapper {
		position: absolute;
		bottom: 0;
		left: 5%;
		z-index: 75;
		background: url(../img/trefle-intro.png) no-repeat top center;
		opacity: 0;
		transition: opacity 400ms ease-in-out 500ms, -webkit-transform 400ms ease-in-out 500ms;
		@include transform(translate(-50px,100px) scale(0.8));

		img {
			transition: opacity 400ms ease-in-out 550ms, -webkit-transform 400ms ease-in-out 550ms;
			opacity: 0;
			@include transform(translate(-50px,100px) scale(0.7)); }

		@include mq(1440px) {
			left: 10%;
			background-size: contain;

			img {
				width: 500px; } }

		@include mq(1280px) {
			bottom: -2px; }

		@include mq(1024px) {
			left: 0; }

		@include mq(750px) {
			display: none; } }

	.text-wrapper {
		position: absolute;
		right: 5%;
		top: 50%;
		@include transform(translate(0,-50%));
		text-align: center;

		@include mq(1440px) {
			right: 10%; }

		@include mq(1024px) {
			z-index: 100;
			right: 5%; }

		@include mq(750px) {
			padding: 100px 0 75px;
			position: static;
			text-align: center;
			width: 100%;
			top: 0;
			right: 0;
			@include transform(translate(0,0)); }

		p {
			text-transform: uppercase;
			color: #FFF;
			@include transform(translate(0,-20px) scale(0.9));
			opacity: 0;
			transition: opacity 400ms ease-in-out, -webkit-transform 400ms ease-in-out;

			&:nth-of-type(1) {
				font-size: 78px;
				@include delay(750ms);

				@include mq(1440px) {
					font-size: 45px; }

				@include mq(480px) {
					font-size: 32.5px; } }


			&:nth-of-type(2) {
				color: $green;
				font-weight: 800;
				font-size: 125px;
				@include delay(850ms);

				@include mq(1440px) {
					font-size: 72.5px; }

				@include mq(480px) {
					font-size: 52px; } }

			&:nth-of-type(3) {
				font-size: 50px;
				@include delay(950ms);
				position: relative;
				overflow: hidden;

				@include mq(1440px) {
					font-size: 29px; }

				@include mq(480px) {
					font-size: 21px; }

				span {
					position: relative;
					z-index: 20;
					display: inline-block;
					padding: 0 20px;
					background: $gray;

					@include mq(750px) {
						background: #040404; } }

				&:after {
					width: 100%;
					content: '';
					height: 1px;
					background: #858687;
					position: absolute;
					z-index: 10;
					top: 50%;
					left: 0; } }


			&:nth-of-type(4) {
				font-weight: 800;
				font-size: 155px;
				@include delay(1050ms);

				@include mq(1440px) {
					font-size: 90px; }

				@include mq(480px) {
					font-size: 65px; } }

			&:nth-of-type(5) {
				margin-top: 10px;
				float: right;
				font-weight: 800;
				font-size: 50px;
				padding: 20px;
				display: inline-block;
				background: #967832;
				background: -moz-linear-gradient(left,  #967832 0%, #ba9a40 100%);
				background: -webkit-linear-gradient(left,  #967832 0%,#ba9a40 100%);
				background: linear-gradient(to right,  #967832 0%,#ba9a40 100%);
				filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#967832', endColorstr='#ba9a40',GradientType=1 );
				@include transform(skew(-10deg,0));
				@include delay(1150ms);

				@include mq(1440px) {
					font-size: 29px; }

				@include mq(750px) {
					float: none;
					margin: 10px auto 0; }

				@include mq(480px) {
					font-size: 21px; }

				span {
					display: block;
					@include transform(skew(10deg,0));
					text-shadow: 0px 0px 8px rgba(15, 15, 15, 0.5); } } } }

	&.trigger {
		.image-wrapper {
			opacity: 1;
			@include transform(translate(0,0) scale(1));

			img {
				opacity: 1;
				@include transform(translate(0,0) scale(1)); } }

		.text-wrapper p {
			opacity: 1;
			@include transform(translate(0,0) scale(1));

			&:nth-of-type(5) {
				@include transform(translate(0,0) scale(1) skew(-10deg,0)); } } } }

.three-block {
	position: relative;
	top: -50px;
	z-index: 65;

	.block {
		position: relative;
		display: inline-block;
		vertical-align: top;
		width: 32%;
		margin-right: 2%;
		border: 6px solid #FFF;
		@extend .box-sizing;
		@include transform(skew(-15deg,0));
		overflow: hidden;
		color: #FFF;
		-webkit-box-shadow: 0px 0px 10px 1px rgba(50, 50, 50, 0.75);
		-moz-box-shadow:    0px 0px 10px 1px rgba(50, 50, 50, 0.75);
		box-shadow:         0px 0px 10px 1px rgba(50, 50, 50, 0.75);

		@include mq(1280px) {
			display: none;
			width: 100%;
			margin-right: 0;
			margin-bottom: 40px; }

		&:before {
			content: '';
			position: absolute;
			display: block;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0; }

		.content {
			position: relative;
			padding: 10px 0;
			z-index: 50;
			@include transform(skew(15deg,0));

			span {
				position: relative;
				display: block;
				text-transform: uppercase; } }

		&.win {
			background: #347b30;
			background: -moz-linear-gradient(left,  #347b30 0%, #7fbc3d 100%);
			background: -webkit-linear-gradient(left,  #347b30 0%,#7fbc3d 100%);
			background: linear-gradient(to right,  #347b30 0%,#7fbc3d 100%);
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#347b30', endColorstr='#7fbc3d',GradientType=1 );

			&:before {
				z-index: 75;
				background: url(../img/block-1-trefle.png) no-repeat;
				background-position: right -175px center;
				background-size: auto 110%; }

			span {

				&:nth-of-type(1) {
					font-weight: 900;
					font-size: 52px;
					left: 30px;
					margin: 10px 0; }

				&:nth-of-type(2) {
					font-weight: 700;
					font-size: 36px;
					left: 20px; }

				&:nth-of-type(3) {
					display: block;
					margin: 8px 0;
					font-weight: 700;
					font-size: 37px;
					padding: 18px 40px;
					left: -30px;
					background: #063308;
					background: -moz-linear-gradient(left,  #063308 58%, #37731f 78%, #7cb93d 99%);
					background: -webkit-linear-gradient(left,  #063308 58%,#37731f 78%,#7cb93d 99%);
					background: linear-gradient(to right,  #063308 58%,#37731f 78%,#7cb93d 99%);
					filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#063308', endColorstr='#7cb93d',GradientType=1 ); }


				&:nth-of-type(4) {
					font-size: 20px;
					margin-bottom: 10px;

					&:before {
						display: inline-block;
						background: url(../img/arrow-circle.png) no-repeat;
						background-size: contain;
						width: 25px;
						height: 25px;
						content: '';
						vertical-align: middle;
						margin-right: 10px; } } } }


		&.excellence {
			background: #967832;
			background: -moz-linear-gradient(left,  #967832 0%, #ba9a40 100%);
			background: -webkit-linear-gradient(left,  #967832 0%,#ba9a40 100%);
			background: linear-gradient(to right,  #967832 0%,#ba9a40 100%);
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#967832', endColorstr='#ba9a40',GradientType=1 );
			text-align: center;
			padding-bottom: 15px;

			span {

				&:nth-of-type(1) {
					font-weight: 700;
					font-size: 30px;

					&:before {
						content: '';
						width: 193px;
						height: 57px;
						display: block;
						margin: 10px auto;
						z-index: 75;
						background: url(../img/block-2-star.png) no-repeat;
						background-position: top center; } }

				&:nth-of-type(2) {
					display: block;
					width: 120%;
					font-weight: 700;
					font-size: 46px;
					left: -12.5%;
					padding: 7px 0;
					margin: 10px 0;
					background: #87692a;
					background: -moz-linear-gradient(left,  #87692a 1%, #472b05 50%, #87692a 100%);
					background: -webkit-linear-gradient(left,  #87692a 1%,#472b05 50%,#87692a 100%);
					background: linear-gradient(to right,  #87692a 1%,#472b05 50%,#87692a 100%);
					filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#87692a', endColorstr='#87692a',GradientType=1 ); }

				&:nth-of-type(3) {
					font-size: 32px;
					left: -7.5%; } } }


		&.subscribe {
			margin-right: 0;
			background: #7f7f80;
			background: -moz-linear-gradient(left,  #7f7f80 0%, #b6b5b8 100%);
			background: -webkit-linear-gradient(left,  #7f7f80 0%,#b6b5b8 100%);
			background: linear-gradient(to right,  #7f7f80 0%,#b6b5b8 100%);
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#7f7f80', endColorstr='#b6b5b8',GradientType=1 );

			&:before {
				width: 152px;
				height: 130px;
				@include transform(skew(15deg,0) translate(-70%,0));
				background: url(../img/block-3-computer.png) no-repeat;
				left: 100%;
				top: 40%; }

			span {

				&:nth-of-type(1) {
					text-align: center;
					display: block;
					margin: 10px 0 15px;
					width: 120%;
					left: -7.5%;
					background: #404041;
					padding: 10px;
					font-weight: 900;
					font-size: 44px; }

				&:nth-of-type(2) {
					font-size: 26px;
					left: 30px; }

				&:nth-of-type(3) {
					font-weight: 700;
					font-size: 36px;
					left: 20px;
					margin-top: 5px; }

				&:nth-of-type(4) {
					left: 5px;
					font-weight: 700;
					font-size: 60px;
					padding-bottom: 17px; } } } } }


.section-promo {
	background: url(../img/bg-promo.jpg) no-repeat top center;
	background-size: cover;

	.content-promo {
		overflow: hidden;
		position: relative;
		padding: 50px 0;

		&:after {
			content: '';
			display: block;
			position: absolute;
			bottom: 0;
			right: 0;
			z-index: 25;
			background: url(../img/gray-trefle.png) no-repeat;
			width: 681px;
			height: 680px;
			@include transform(translate(30%,30%));
			animation: rotate-trefle 120s linear infinite;

			@include mq(1280px) {
				width: 400px;
				height: 400px;
				background-size: contain; } } }

	.block-text {
		display: inline-block;
		vertical-align: top;
		width: 47.5%;
		position: relative;
		z-index: 50;

		&:last-of-type {
			margin-left: 5%;

			&:before {
				content: '';
				display: block;
				position: absolute;
				height: 75%;
				top: 0;
				width: 6px;
				left: -5%;
				background: url(../img/gray-dot.png) repeat-y; } }

		@include mq(1024px) {
			width: 100%;
			text-align: center;

			&:last-of-type {
				margin-left: 0;
				margin-top: 100px;

				&:before {
					display: none; } } }


		h2 {
			font-size: 50px;
			text-transform: uppercase;
			font-weight: bold;
			color: #000;

			@include mq(1024px) {
				font-size: 38px; } }

		h3 {
			color: $green;
			font-size: 30px;
			text-transform: uppercase;
			margin-bottom: 40px;

			@include mq(1024px) {
				font-size: 24px; } }

		p {
			margin-bottom: 20px;
			line-height: 125%;
			font-size: 16px; }

		ul {
			padding-left: 20px;
			list-style-type: disc;

			@include mq(1024px) {
				padding-left: 0; }

			li {
				font-size: 16px;
				margin-bottom: 10px;

				@include mq(1024px) {
					display: inline-block;
					vertical-align: top;
					width: 47.5%;
					margin-right: 5%;
					text-align: left;

					&:before {
						content: '- '; }

					&:nth-of-type(2n) {
						margin-right: 0; } }

				@include mq(768px) {
					width: 100%;
					margin-right: 0;
					text-align: center; } } }

		.button-link {
			margin-top: 20px; } } }

.section-user {
	background: #000;

	.header-user {
		position: relative;
		text-align: center;

		&:before {
			height: 1px;
			width: 100%;
			content: '';
			position: absolute;
			left: 0;
			top: 10px;
			border-top: 3px dotted #FFF; }

		&:after {
			content: '';
			position: absolute;
			z-index: 25;
			bottom: 0;
			left: 0;
			width: 100%;
			padding-bottom: 5%;
			background: url(../img/header-user-bg.png) no-repeat;
			background-size: cover;
			@include transform(translate(0,100%)); }


		button {
			position: relative;
			top: -20px;
			z-index: 50;
			display: block;
			text-indent: -9999px;
			width: 47px;
			height: 47px;
			background: url(../img/navigation-arrow.png) no-repeat;
			background-size: cover;
			border: none;
			margin: 0 auto; }

		h2 {
			text-transform: uppercase;
			color: #FFF;
			font-size: 30px;
			margin-top: 30px;

			@include mq(1280px) {
				max-width: 80%;
				margin: 30px auto 0; }

			strong {
				font-weight: bold; } }

		.button-link {
			margin: 40px 0 60px; } }

	.content-user {
		background: #484848 url(../img/bg-user.jpg) no-repeat top center;
		padding: 275px 0 75px;
		text-align: center;
		position: relative;

		@include mq(1280px) {
			padding: 150px 0 75px; }

		@include mq(768px) {
			padding: 100px 0 75px; }

		&:before, &:after {
			position: absolute;
			display: block;
			content: '';
			background: url(../img/gray-trefle.png) no-repeat;
			width: 681px;
			height: 680px;
			opacity: 0.3; }

		&:before {
			top: -10%;
			left: 0;
			@include transform(scale(0.3) translate(-150%,0));
			animation: rotate-trefle-small 120s linear infinite;

			@include mq(1280px) {
				top: 25%;
				width: 400px;
				height: 400px;
				background-size: cover; } }

		&:after {
			bottom: 0;
			left: 0;
			@include transform(scale(0.8) translate(-40%,66%));
			animation: rotate-trefle-large 120s linear infinite;

			@include mq(1280px) {
				width: 400px;
				height: 400px;
				background-size: cover; } }

		h2 {
			text-transform: uppercase;
			font-weight: bold;
			color: #FFF;
			font-size: 80px;

			span {
				font-size: 30px;
				font-weight: normal;
				display: block; }

			@include mq(768px) {
				font-size: 50px;

				span {
					font-size: 24px; } }

			@include mq(480px) {
				font-size: 38px; } }

		h3 {
			font-size: 50px;
			text-transform: uppercase;
			font-weight: bold;
			color: $green;

			@include mq(768px) {
				font-size: 38px; } }

		.button-link {
			margin-top: 55px; } } }

.section-temoignage {
	padding: 75px 0;
	text-align: center;

	h2 {
		text-transform: uppercase;
		font-size: 30px;
		font-weight: bold; } }


