.contact {
	.intro-contact {
		position: relative;
		background: $gray url(../img/bg-contact.jpg) no-repeat center;
		background-size: cover;
		background-attachment: fixed;
		padding: 200px 0 150px;
		text-align: center;

		@include mq(768px) {
			padding: 75px 0;
			background-attachment: scroll; }

		&:after,&:before {
			content: '';
			height: 5px;
			width: 100%;
			position: absolute;
			left: 0;
			background: url(../img/white-dot.png) repeat-x;
			background-position: 0 -5px; }

		&:before {
			bottom: 20px; }

		&:after {
			top: 20px; }

		h2 {
			color: #FFF;
			text-transform: uppercase;
			font-size: 42px;
			font-weight: bold; }

		p {
			display: block;
			margin-top: 15px;
			font-size: 18px;
			color: #FFF;
			margin-top: 15px;
			line-height: 125%;

			&.required {
				color: $green; } } }

	#form-contact {
		padding: 75px 0;

		.input-block {
			display: inline-block;
			width: 47.5%;
			margin-right: 5%;
			margin-bottom: 30px;

			&:nth-of-type(2n) {
				margin-right: 0; }

			@include mq(768px) {
				width: 100%;
				margin-right: 0; }

			label {
				display: block;
				margin-bottom: 5px;
				color: #000; }

			.input-wrapper {
				width: 100%;
				border: 2px solid #dddddd; }

			&.message {
				width: 100%;
				margin-right: 0;

				textarea {
					@extend .box-sizing;
					width: 100%;
					height: 150px; } } } } }
