@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,200,300,600,700,900);

@mixin border-radius($data) {
	border-radius: $data;
	-webkit-border-radius: $data;
	-moz-border-radius: $data; }

@mixin transform($data) {
	-webkit-transform: $data;
	-ms-transform: $data;
	transform: $data; }

@mixin delay($data) {
	-webkit-transition-delay: $data;
	-ms-transition-delay: $data;
	transition-delay: $data; }

@mixin mq($size) {
	@media screen and (max-width:$size) {
		@content; } }

@mixin mq-hide($size) {
	@media screen and (max-width:$size) {
		display: none !important; } }

$gold: #b89840;
$white: #fcfcfc;
$green: #8ac53f;
$gray: #171717;

$source: 'Source Sans Pro', sans-serif;

@keyframes rotate-trefle {
	0% {
		@include transform(translate(30%,30%) rotate(0deg)); }

	100% {
		@include transform(translate(30%,30%) rotate(360deg)); } }

@keyframes rotate-trefle-small {
	0% {
		@include transform(scale(0.3) translate(-150%,0) rotate(0deg)); }

	100% {
		@include transform(scale(0.3) translate(-150%,0) rotate(-360deg)); } }

@keyframes rotate-trefle-large {
	0% {
		@include transform(scale(0.8) translate(-40%,66%) rotate(0deg)); }

	100% {
		@include transform(scale(0.8) translate(-40%,66%) rotate(360deg)); } }

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
	font-family: $source; }

sup {
	vertical-align: super;
	font-size: 50%; }

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
	display: block; }

body {
	line-height: 1; }

ol, ul {
	list-style: none; }

blockquote, q {
	quotes: none; }

blockquote:before, blockquote:after, q:before, q:after {
	content: '';
	content: none; }

table {
	border-collapse: collapse;
	border-spacing: 0; }

a {
	text-decoration: none; }

.clearfix:before,
.clearfix:after {
	content: " ";
	display: table; }

.clearfix:after {
	clear: both; }

button:focus, input:focus {
	outline: none; }

button {
	font-family: $source;
	cursor: pointer;
	border: none; }

.wrapper {
	margin: 0 auto;
	padding: 0 5%;
	max-width: 1216px;
	width: 90%; }

.black-overlay {
	position: fixed;
	z-index: 75;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	visibility: hidden;
	transition: background 250ms ease, visibility 0ms ease 250ms;
	-webkit-transition: background 250ms ease, visibility 0ms ease 250ms;
	-ms-transition: background 250ms ease, visibility 0ms ease 250ms;

	&.trigger {
		background: rgba(0,0,0,0.8);
		visibility: visible;
		transition-delay: 0ms;
		-webkit-transition-delay: 0ms;
		-ms-transition-delay: 0ms; } }


input, textarea, select {
	border: 1px solid transparent;
	padding: 20px;
	background: #FFF;
	font-size: 18px;
	transition: background 250ms ease, border 250ms ease;
	-webkit-transition: background 250ms ease, border 250ms ease;
	-ms-transition: background 250ms ease, border 250ms ease;
	font-family: $source;

	&.form-error {
		border: 1px solid #ff9b9b;
		background: #ffd9d9; } }

.box-sizing {
	box-sizing: border-box;
	-ms-box-sizing: border-box;
	-webkit-box-sizing: border-box; }

.custom-text {
	h1 {
		font-size: 42px;
		margin-top: 20px;
		font-weight: bold; }

	h2 {
		font-size: 32px;
		margin-top: 20px;
		font-weight: bold; }

	h3 {
		font-size: 24px;
		margin-top: 20px;
		font-weight: bold; }

	p {
		font-size: 18px;
		margin-top: 20px;
		line-height: 22px; }

	a {
		font-size: 18px;
		color: #00a4de;

		&:hover {
			text-decoration: underline; } }

	strong {}

	ul {
		list-style-type: disc;
		padding-left: 20px;

		li {
			font-size: 18px;
			margin-top: 8px; } } }

.button-link {
	display: inline-block;
	padding: 20px 40px;
	@include transform(skew(-15deg,0));

	@include mq(1280px) {
		padding: 15px 30px; }

	span {
		display: inline-block;
		vertical-align: middle;
		text-transform: uppercase;
		@include transform(skew(15deg,0));
		color: #FFF;
		font-size: 26px;

		@include mq(1280px) {
			font-size: 20px; }

		&:after {
			position: relative;
			top: -2px;
			content: '';
			background: url(../img/arrow-circle.png) repeat-x;
			background-size: cover;
			width: 25px;
			height: 25px;
			display: inline-block;
			vertical-align: middle;
			margin-left: 10px;
			transition: background 250ms ease;
			-webkit-transition: background 250ms ease;
			-ms-transition: background 250ms ease; } }

	&.green {
		background: $green; }

	&.gold {
		background: $gold; }

	&:hover {

		span {

			&:after {
				background-position: 25px 0; } } } }

.message-box {
	position: fixed;
	z-index: 5000;
	right:  0;
	bottom: 20px;
	width: 350px;
	overflow: hidden;

	li {
		font-family: $source;
		width: 100%;
		display: block;
		margin: 0 0 5px 0;
		padding: 15px 20px 15px 15px;
		color: #FFF;
		font-weight: 700;
		font-size: 18px;
		transform: translate(0,0);
		-webkit-transform: translate(0,0);
		-ms-transform: translate(0,0);
		transition: transform 250ms ease;
		-webkit-transition: -webkit-transform 250ms ease;
		-ms-transition: transform 250ms ease;
		@extend .box-sizing;

		&.success {
			background: #5cc148; }

		&.error {
			background: #d24444; }

		&.new {
			transform: translate(100%,0);
			-webkit-transform: translate(100%,0);
			-ms-transform: translate(100%,0); } } }

.input-block {
	position: relative;
	z-index: 250;

	ul {
		position: absolute;
		background: #FFF;
		bottom: 0;
		right: 9px;
		@include transform(translate(0,100%));
		border: 1px solid #CCC;

		li {
			cursor: pointer;
			display: block;
			font-size: 16px;
			color: #000;
			padding: 10px; } }

	label {
		display: inline-block;
		vertical-align: middle;
		color: #FFF;
		font-size: 24px; }

	.input-wrapper {
		display: inline-block;
		vertical-align: middle;
		background: #dddddd;
		@include transform(skew(-15deg,0));
		@extend .box-sizing;
		border: 2px solid transparent;
		transition: border 250ms ease, background 250ms ease;
		overflow: hidden;

		&.form-error {
			border: 2px solid #d24444;
			background: #ffefef; }

		input, select, textarea {
			background: none;
			display: block;
			@include transform(skew(15deg,0));
			@extend .box-sizing;
			width: 100%;
			outline: none;
			font-size: 18px;
			transition: color 250ms ease; }

		&.focus {
			background: #FFF; }

		&.filled {
			input {
				color: #535353; } }

		&.select {
			position: relative;

			&:after {
				content: '';
				display: block;
				position: absolute;
				top: 50%;
				right: 10px;
				@include transform(translate(0,-50%) skew(15deg,0));
				background: url(../img/select-arrow.png) no-repeat;
				width: 25px;
				height: 25px; } } } }

.fix-content {
	padding: 150px 0;

	h2 {
		margin-top: 50px; } }

.temoignage {
	display: inline-block;
	@extend .box-sizing;
	@include border-radius(50%);
	border: 7px solid #FFF;
	background: #000;
	position: relative;
	width: 30%;
	margin: 50px 1.5% 0;
	padding-top: 30%;
	-webkit-box-shadow: 0px 0px 47px -7px rgba(0,0,0,0.67);
	-moz-box-shadow: 0px 0px 47px -7px rgba(0,0,0,0.67);
	box-shadow: 0px 0px 47px -7px rgba(0,0,0,0.67);

	@include mq(1280px) {
		width: 47.5%;
		padding-top: 47.5%;
		margin: 50px 5% 0 0;

		&:nth-of-type(2n) {
			margin-right: 0; } }

	@include mq(768px) {
		width: 70%;
		padding-top: 70%;
		margin: 50px 15% 0 15%;

		&:nth-of-type(2n) {
			margin-right: 15%; } }

	@include mq(640px) {
		width: 100%;
		padding-top: 100%;
		margin: 50px 0 0;

		&:nth-of-type(2n) {
			margin-right: 0; } }

	.circle-content {
		position: absolute;
		width: 90%;
		top: 50%;
		left: 50%;
		@include transform(translate(-50%,-50%)); }

	.name {
		text-transform: uppercase;
		color: $green;
		font-size: 28px;

		@include mq(480px) {
			max-width: 80%;
			display: block;
			margin: 0 auto; } }

	.text {
		margin-top: 10px;
		max-height: 125px;
		color: #FFF;

		p {
			color: #FFF;
			font-size: 14px;
			line-height: 125%; } }

	.link {
		position: relative;
		display: block;
		margin: 20px auto 0;
		color: $green;
		width: 25px;
		height: 25px;
		font-size: 20px;
		@extend .box-sizing;
		border: 2px solid $green;
		@include border-radius(50%);

		span {
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			@include transform(translate(-50%,-50%)); } } }




