.la-compagnie {
	overflow: hidden;

	.concept {
		position: relative;
		background: $gray;
		padding: 100px 0;
		background: url(../img/bg-intro.jpg) no-repeat;
		background-size: cover;

		@include mq(1440px) {
			background-position: -225px center; }

		@include mq(1024px) {
			text-align: center;
			background: $gray; }

		&:after,&:before {
			content: '';
			height: 5px;
			width: 100%;
			position: absolute;
			left: 0;
			background: url(../img/white-dot.png) repeat-x;
			background-position: 0 -5px; }

		&:before {
			bottom: 20px; }

		&:after {
			top: 20px; }

		.image-wrapper {
			width: 47.5%;
			display: inline-block;
			vertical-align: middle;
			margin-right: 5%;

			@include mq(1024px) {
				width: 40%;
				margin: 0 auto; }

			@include  mq(640px) {
				width: 75%; }


			img {
				max-width: 100%;
				display: block;
				margin: 0 auto; } }

		.content {
			width: 47.5%;
			display: inline-block;
			vertical-align: middle;

			@include mq(1024px) {
				width: 100%;
				margin-top: 50px; }

			h3 {
				color: $green;
				text-transform: uppercase;
				font-size: 34px;
				font-weight: lighter;

				@include mq(1024px) {
					font-size: 26px; } }

			h2 {
				color: #FFF;
				text-transform: uppercase;
				font-weight: bold;
				font-size: 56px;
				margin-top: 5px;

				@include mq(1024px) {
					font-size: 42px; } }

			p {
				margin-top: 20px;
				color: #FFF;
				font-size: 16px;
				line-height: 125%; } } }

	.monthly-contest {
		padding: 100px 0;
		background: url(../img/bg-promo.jpg) no-repeat;
		background-size: cover;
		position: relative;

		@include  mq(1024px) {
			text-align: center; }

		.content {
			width: 47.5%;
			display: inline-block;
			vertical-align: middle;
			margin-right: 5%;

			@include  mq(1024px) {
				width: 100%;
				margin: 0; }

			h3 {
				color: $green;
				text-transform: uppercase;
				font-size: 34px;
				font-weight: lighter;

				@include mq(1024px) {
					font-size: 26px; } }

			h2 {
				color: #000;
				text-transform: uppercase;
				font-weight: bold;
				font-size: 56px;
				margin-top: 5px;

				@include mq(1024px) {
					font-size: 42px; } }

			p {
				margin-top: 20px;
				color: #000;
				font-size: 16px;
				line-height: 125%; } }

		.image-wrapper {
			position: relative;
			width: 47.5%;
			display: inline-block;
			vertical-align: middle;

			&:before {
				position: absolute;
				left: -5%;
				top: 20%;
				content: '';
				width: 5px;
				height: 60%;
				background: url(../img/gray-dot.png)  repeat-y; }

			img {
				dispaly: block;
				max-width: 100%;
				margin: 0 auto; }

			@include  mq(1024px) {
				width: 40%;
				margin: 50px auto 0; }

			@include  mq(640px) {
				width: 75%;

				&:before {
					display: none; } } } }

	.simple-concept {
		background: url(../img/bg-concept.jpg) no-repeat center;
		background-size: cover;
		text-align: center;
		padding: 200px 0;
		position: relative;

		@include mq(1024px) {
			padding: 100px 0 150px; }

		&:after {
			position: absolute;
			left: 0;
			bottom: -20px;
			content: '';
			width: 100%;
			height: 5px;
			background: url(../img/gray-dot.png)  repeat-x;
			background-position: 0 -5px; }

		.wrapper {
			position: relative;
			z-index: 75;
			max-width: 850px; }

		h2 {
			text-transform: uppercase;
			font-weight: bold;
			font-size: 60px;
			color: #FFF;

			@include mq(1024px) {
				font-size: 42px; } }

		h3 {
			display: block;
			text-transform: uppercase;
			font-weight: bold;
			color: $green;
			font-size: 50px;
			margin: 50px 0 30px;

			@include mq(1024px) {
				font-size: 32px;
				margin-top: 20px; } }

		p {
			margin-top: 20px;
			font-size: 16px;
			line-height: 150%;
			color: #FFF; } }

	.temoignage-concept {
		padding: 75px 0;
		text-align: center;
		position: relative;

		&:before, &:after {
			content: '';
			position: absolute;
			z-index: 50;
			background: url(../img/gray-trefle.png) no-repeat;
			width: 681px;
			height: 680px;
			background-size: contain;
			animation: rotate-trefle 120s linear infinite; }

		&:before {
			right: -75px;
			top: -500px; }

		&:after {
			bottom: -115px;
			left: -510px; }

		@include mq(1440px) {
			&:before, &:after {
				width: 400px;
				height: 400px; }

			&:before {
				top: -300px; }

			&:after {
				left: -300px;
				bottom: -50px; } }

		@include mq(768px) {
			&:before, &:after {
				width: 250px;
				height: 250px; }

			&:before {
				top: -175px;
				right: -25px; }

			&:after {
				left: -175px;
				bottom: 0; } }



		.wrapper {
			position: relative;
			z-index: 75; }

		h2 {
			text-transform: uppercase;
			font-weight: bold;
			font-size: 30px;
			color: #000; } } }




