.footer {
	background: url(../img/bg-footer.jpg) no-repeat;
	background-position: 10% center;
	background-size: cover;
	padding: 30px 0;
	border-top: 30px solid $gold;
	position: relative;

	@include mq(1440px) {
		background-size: auto 100%;
		background-position: -250px center; }

	@include mq(1024px) {
		background: $gray; }

	&:before {
		content: '';
		display: block;
		width: 100%;
		position: absolute;
		top: -16px;
		left: 0;
		height: 1px;
		border-top: 3px dotted #FFF; }

	.logo {
		position: relative;
		left: 6%;
		max-width: 300px;
		display: inline-block;

		img {
			width: 100%; }

		@include mq(1280px) {
			left: 5%;
			max-width: 250px;
			margin-right: 15px; }

		@include mq(1024px) {
			display: block;
			position: static;
			left: 0;
			margin: 0 auto; } }

	.content-footer {
		float: right;
		margin-top: 15px;

		@include mq(1024px) {
			float: none;
			display: block; }

		ul {
			display: inline-block;
			vertical-align: bottom;
			width: 350px;

			@include mq(1024px) {
				width: 100%;
				text-align: center; }

			li {
				display: inline-block;
				vertical-align: top;
				width: 47.5%;
				margin-bottom: 25px;

				&:nth-of-type(2n) {
					margin-left: 5%; }

				@include mq(1024px) {
					width: auto;
					margin: 0 10px 25px;

					&:nth-of-type(2n) {
						margin-left: 0; } }

				@include mq(320px) {
					width: 100%;
					margin: 0 0 25px 0; }

				a {
					display: block;
					color: #FFF;
					text-transform: uppercase;
					font-size: 16px;
					margin-top: 10px;

					@include mq(1024px) {
						text-align: center;
						margin: 10px auto 0;

						&.additionnal-link {
							display: none; } }

					&:first-of-type {
						margin-top: 0;
						font-weight: bold;
						font-size: 20px; } }

				&:last-of-type {
					position: relative;
					@include transform(translate(0,-100%));

					@include mq(1024px) {
						@include transform(translate(0,0)); } }

				&:nth-last-of-type(-n+2) {
					margin-bottom: 0;

					@include mq(1024px) {
						margin-bottom: 25px; } } } }


		.right-footer {
			display: inline-block;
			vertical-align: bottom;
			padding-bottom: 18px;
			text-align: right;

			@include mq(1024px) {
				width: 100%;
				text-align: center; }

			a {
				color: $gold;
				font-weight: bold;
				text-transform: uppercase;
				font-size: 22px;
				display: inline-block;
				margin-bottom: 20px;

				&:before {
					content: '';
					display: inline-block;
					margin-right: 10px;
					vertical-align: bottom;
					width: 23px;
					height: 29px;
					background: url(../img/header-sprite.png) no-repeat;
					background-size: cover; } }

			p {
				display: block;
				color: #FFF;
				margin-top: 10px;
				font-size: 16px;

				@include mq(1024px) {
					width: 100%; } } } } }
